import trelloEnums from '../shared/trello-enums';
import selector from '../shared/capability-selector';

const get = async t => {

    const scope = trelloEnums.Scope.Card;
    const visibility = trelloEnums.Visibility.Shared;
    const key = trelloEnums.Key.LogEntries;
    const capability = trelloEnums.Capability.CardDetailBadges;

    const log = await t.get(scope, visibility, key);
    const prefs = await t.get(scope, visibility, trelloEnums.Key.ChallengePreferences);

    console.log(`Data stored for ${trelloEnums.Key.ChallengePreferences} against ${scope} with ${visibility} access for ${capability}  : `,  prefs);

    const card = await t.card('name').get('name');

    console.log('card: ', JSON.stringify(card, null, 2));

    return [
        {
            icon: GRAY_ICON,
            text: challengeText,
            condition: trelloEnums.Condition.Always,
            callback: function(t){
                return t.modal({
                    title: "Challenge",                    
                    args: { type: trelloEnums.Type.Challenge },
                    url: './modals/challenge.html',
                    fullscreen: false,
                    height: 500,                    
                    callback: onCloseChallengToolbox,
                });
            }
        }, 
    ]
};    

export const getCardDetailBadges = (t, opts) => {
    return get(t);
}